*{
    box-sizing: border-box;
}

body {
    font-family: "inter";
    margin: 0;
}

nav {
    display: flex;
    align-items: center;
    background-color: #21222A;
    height: 90px;
    padding: 30px 25px;
}

.navbar-logo {
    width: 65px;
    margin-right: 7px;
}

.navbar-logo-text {
    margin: 0;
    margin-right: auto;

    color: #61DAFB;
    font-size: 22px;
}

.navbar-title {
    margin: 0;
    color: #DEEBF8;
    font-weight: 600;
}